.contact {
    background: linear-gradient(135deg, var(--color-bg-variant) 0%, var(--color-bg) 100%);
    padding: 6rem 0;
}

.contact__intro {
    text-align: center;
    max-width: 700px;
    margin: 0 auto 4rem;
    color: var(--color-light);
    font-size: 1.1rem;
    line-height: 1.6;
}

.contact__container {
    display: grid;
    grid-template-columns: 1fr 1.5fr;
    gap: 4rem;
    margin-bottom: 4rem;
}

.contact__card {
    background: rgba(255, 255, 255, 0.05);
    border: 1px solid rgba(77, 181, 255, 0.1);
    border-radius: 1.5rem;
    padding: 2.5rem;
    height: fit-content;
    position: sticky;
    top: 2rem;
}

.contact__card h3 {
    color: var(--color-white);
    font-size: 1.5rem;
    margin-bottom: 1rem;
    font-weight: 600;
}

.contact__card>p {
    color: var(--color-light);
    line-height: 1.6;
    margin-bottom: 2rem;
}

.contact__methods {
    margin-bottom: 2rem;
}

.contact__method {
    display: flex;
    align-items: center;
    gap: 1rem;
    padding: 1rem;
    background: rgba(255, 255, 255, 0.03);
    border-radius: 0.8rem;
    margin-bottom: 1rem;
    transition: var(--transition);
}

.contact__method:hover {
    background: rgba(255, 255, 255, 0.06);
    transform: translateX(5px);
}

.method__icon {
    font-size: 1.5rem;
    color: var(--color-primary);
    width: 50px;
    height: 50px;
    background: rgba(77, 181, 255, 0.1);
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-shrink: 0;
}

.method__content h4 {
    color: var(--color-white);
    font-size: 1rem;
    margin-bottom: 0.3rem;
    font-weight: 600;
}

.method__content p {
    color: var(--color-light);
    font-size: 0.9rem;
    margin-bottom: 0.3rem;
}

.method__link {
    color: var(--color-primary);
    text-decoration: none;
    font-weight: 500;
    transition: var(--transition);
}

.method__link:hover {
    color: var(--color-accent);
}

.method__value {
    color: var(--color-light);
    font-weight: 500;
}

.contact__social {
    margin-bottom: 2rem;
    padding-top: 1.5rem;
    border-top: 1px solid rgba(77, 181, 255, 0.1);
}

.contact__social h4 {
    color: var(--color-white);
    font-size: 1rem;
    margin-bottom: 1rem;
    font-weight: 600;
}

.social__links {
    display: flex;
    flex-direction: column;
    gap: 0.8rem;
}

.social__link {
    display: flex;
    align-items: center;
    gap: 0.8rem;
    padding: 0.8rem;
    background: rgba(255, 255, 255, 0.03);
    border-radius: 0.5rem;
    color: var(--color-light);
    text-decoration: none;
    transition: var(--transition);
}

.social__link:hover {
    background: var(--social-color, var(--color-primary));
    color: var(--color-white);
    transform: translateX(5px);
}

.contact__availability {
    padding-top: 1.5rem;
    border-top: 1px solid rgba(77, 181, 255, 0.1);
}

.contact__availability h4 {
    color: var(--color-white);
    font-size: 1rem;
    margin-bottom: 0.8rem;
    font-weight: 600;
}

.contact__availability p {
    color: var(--color-light);
    font-size: 0.9rem;
    line-height: 1.5;
}

.contact__form {
    background: rgba(255, 255, 255, 0.05);
    border: 1px solid rgba(77, 181, 255, 0.1);
    border-radius: 1.5rem;
    padding: 2.5rem;
    margin-bottom: 2rem;
}

.contact__form h3 {
    color: var(--color-white);
    font-size: 1.5rem;
    margin-bottom: 2rem;
    font-weight: 600;
}

.form__group {
    margin-bottom: 1.5rem;
}

.form__row {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 1rem;
}

.form__field {
    margin-bottom: 1.5rem;
}

.form__field label {
    display: block;
    color: var(--color-white);
    font-weight: 500;
    margin-bottom: 0.5rem;
}

.form__field input,
.form__field select,
.form__field textarea {
    width: 100%;
    padding: 0.8rem 1rem;
    background: rgba(255, 255, 255, 0.05);
    border: 1px solid rgba(77, 181, 255, 0.2);
    border-radius: 0.5rem;
    color: var(--color-white);
    font-size: 0.95rem;
    transition: var(--transition);
}

.form__field input:focus,
.form__field select:focus,
.form__field textarea:focus {
    outline: none;
    border-color: var(--color-primary);
    background: rgba(255, 255, 255, 0.08);
    box-shadow: 0 0 0 3px rgba(77, 181, 255, 0.1);
}

.form__field input::placeholder,
.form__field textarea::placeholder {
    color: rgba(255, 255, 255, 0.5);
}

.form__field select {
    cursor: pointer;
}

.form__field textarea {
    resize: vertical;
    min-height: 120px;
}

.form__submit {
    width: 100%;
    padding: 1rem 2rem;
    background: linear-gradient(45deg, var(--color-primary), var(--color-accent));
    color: var(--color-white);
    border: none;
    border-radius: 0.8rem;
    font-size: 1rem;
    font-weight: 600;
    cursor: pointer;
    transition: var(--transition);
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 0.5rem;
    margin-top: 1rem;
}

.form__submit:hover:not(:disabled) {
    background: linear-gradient(45deg, var(--color-accent), var(--color-primary));
    transform: translateY(-2px);
    box-shadow: var(--shadow-medium);
}

.form__submit:disabled {
    opacity: 0.7;
    cursor: not-allowed;
}

.spinner {
    width: 20px;
    height: 20px;
    border: 2px solid rgba(255, 255, 255, 0.3);
    border-top: 2px solid var(--color-white);
    border-radius: 50%;
    animation: spin 1s linear infinite;
}

@keyframes spin {
    0% {
        transform: rotate(0deg);
    }

    100% {
        transform: rotate(360deg);
    }
}

.form__message {
    margin-top: 1rem;
    padding: 1rem;
    border-radius: 0.5rem;
    display: flex;
    align-items: center;
    gap: 0.5rem;
    font-weight: 500;
}

.form__message.success {
    background: rgba(76, 175, 80, 0.1);
    border: 1px solid rgba(76, 175, 80, 0.3);
    color: #4CAF50;
}

.form__message.error {
    background: rgba(244, 67, 54, 0.1);
    border: 1px solid rgba(244, 67, 54, 0.3);
    color: #F44336;
}

.quick__actions {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 1rem;
}

.quick__action {
    display: flex;
    align-items: center;
    gap: 1rem;
    padding: 1.5rem;
    background: rgba(255, 255, 255, 0.05);
    border: 1px solid rgba(77, 181, 255, 0.1);
    border-radius: 1rem;
    color: var(--color-white);
    text-decoration: none;
    transition: var(--transition);
}

.quick__action:hover {
    background: rgba(255, 255, 255, 0.08);
    border-color: var(--color-primary);
    transform: translateY(-5px);
    box-shadow: var(--shadow-medium);
}

.quick__action svg {
    font-size: 2rem;
    color: var(--color-primary);
    flex-shrink: 0;
}

.quick__action h4 {
    font-size: 1rem;
    margin-bottom: 0.3rem;
    font-weight: 600;
}

.quick__action p {
    color: var(--color-light);
    font-size: 0.9rem;
}

.contact__footer {
    text-align: center;
}

.contact__guarantee {
    background: rgba(255, 255, 255, 0.03);
    border: 1px solid rgba(77, 181, 255, 0.1);
    border-radius: 1.5rem;
    padding: 3rem;
}

.contact__guarantee h3 {
    color: var(--color-white);
    font-size: 1.8rem;
    margin-bottom: 2rem;
}

.guarantee__items {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    gap: 2rem;
}

.guarantee__item {
    text-align: center;
}

.guarantee__item h4 {
    color: var(--color-primary);
    font-size: 1.2rem;
    margin-bottom: 0.8rem;
    font-weight: 600;
}

.guarantee__item p {
    color: var(--color-light);
    line-height: 1.5;
}

/* Responsive Design */
@media screen and (max-width: 1024px) {
    .contact__container {
        grid-template-columns: 1fr;
        gap: 2rem;
    }

    .contact__card {
        position: static;
    }

    .form__row {
        grid-template-columns: 1fr;
    }
}

@media screen and (max-width: 768px) {
    .contact {
        padding: 4rem 0;
    }

    .contact__card,
    .contact__form {
        padding: 2rem;
    }

    .quick__actions {
        grid-template-columns: 1fr;
    }

    .guarantee__items {
        grid-template-columns: 1fr;
        gap: 1rem;
    }

    .contact__guarantee {
        padding: 2rem;
    }
}

@media screen and (max-width: 600px) {

    .contact__card,
    .contact__form {
        padding: 1.5rem;
    }

    .contact__method {
        flex-direction: column;
        text-align: center;
        gap: 0.8rem;
    }

    .quick__action {
        flex-direction: column;
        text-align: center;
        gap: 0.8rem;
    }

    .quick__action svg {
        font-size: 1.5rem;
    }
}