.about {
    padding: 6rem 0;
}

.about__container {
    display: flex;
    flex-direction: column;
    gap: 4rem;
}

.about__content {
    display: grid;
    grid-template-columns: 1fr 2fr;
    gap: 4rem;
    align-items: center;
}

.about__image {
    position: relative;
    border-radius: 2rem;
    overflow: hidden;
}

.about__img {
    width: 100%;
    height: 400px;
    object-fit: cover;
    transition: var(--transition);
}

.about__image-overlay {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    background: linear-gradient(transparent, rgba(0, 0, 0, 0.8));
    padding: 2rem;
    color: var(--color-white);
    opacity: 0;
    transition: var(--transition);
}

.about__image:hover .about__image-overlay {
    opacity: 1;
}

.about__image:hover .about__img {
    transform: scale(1.1);
}

.overlay__text {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
}

.overlay__text span:first-child {
    font-size: 0.9rem;
    color: var(--color-light);
}

.overlay__text span:last-child {
    font-size: 1.2rem;
    color: var(--color-accent);
    font-weight: 600;
}

.about__text h3 {
    color: var(--color-white);
    font-size: 2rem;
    margin-bottom: 1.5rem;
    font-weight: 600;
}

.about__text p {
    color: var(--color-light);
    line-height: 1.8;
    margin-bottom: 1.5rem;
    font-size: 1.1rem;
}

.about__stats {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
    margin: 2rem 0;
}

.stat__item {
    text-align: center;
    padding: 1.5rem;
    background: rgba(255, 255, 255, 0.05);
    border: 1px solid rgba(77, 181, 255, 0.1);
    border-radius: 1rem;
    transition: var(--transition);
}

.stat__item:hover {
    background: rgba(255, 255, 255, 0.08);
    border-color: var(--color-primary);
    transform: translateY(-5px);
}

.stat__item h4 {
    color: var(--color-primary);
    font-size: 2rem;
    font-weight: 700;
    margin-bottom: 0.5rem;
}

.stat__item span {
    color: var(--color-light);
    font-size: 0.9rem;
}

.about__actions {
    display: flex;
    gap: 1.5rem;
    margin-top: 2rem;
}

.btn-outline {
    background: transparent;
    color: var(--color-primary);
    border: 1px solid var(--color-primary);
}

.btn-outline:hover {
    background: var(--color-primary);
    color: var(--color-white);
}

.about__expertise h3,
.about__skills h3,
.about__journey h3 {
    color: var(--color-white);
    font-size: 1.8rem;
    margin-bottom: 2rem;
    text-align: center;
}

.expertise__grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    gap: 2rem;
}

.about__card {
    background: rgba(255, 255, 255, 0.05);
    border: 1px solid rgba(77, 181, 255, 0.1);
    border-radius: 1.5rem;
    padding: 2rem;
    text-align: center;
    transition: var(--transition);
}

.about__card:hover {
    background: rgba(255, 255, 255, 0.08);
    border-color: var(--color-primary);
    transform: translateY(-10px);
    box-shadow: var(--shadow-heavy);
}

.card__icon {
    font-size: 3rem;
    color: var(--color-primary);
    margin-bottom: 1.5rem;
}

.about__card h4 {
    color: var(--color-white);
    font-size: 1.3rem;
    margin-bottom: 1rem;
    font-weight: 600;
}

.about__card p {
    color: var(--color-light);
    line-height: 1.6;
    margin-bottom: 1.5rem;
}

.about__card ul {
    list-style: none;
    padding: 0;
    text-align: left;
}

.about__card li {
    color: var(--color-light);
    margin-bottom: 0.5rem;
    position: relative;
    padding-left: 1rem;
    font-size: 0.9rem;
}

.about__card li::before {
    content: 'â–¶';
    position: absolute;
    left: 0;
    color: var(--color-accent);
    font-size: 0.8rem;
}

.skills__container {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
    gap: 2rem;
}

.skill__item {
    background: rgba(255, 255, 255, 0.05);
    border: 1px solid rgba(77, 181, 255, 0.1);
    border-radius: 1rem;
    padding: 1.5rem;
    transition: var(--transition);
}

.skill__item:hover {
    background: rgba(255, 255, 255, 0.08);
    border-color: var(--color-primary);
}

.skill__header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 1rem;
}

.skill__name {
    color: var(--color-white);
    font-weight: 600;
}

.skill__category {
    color: var(--color-accent);
    font-size: 0.8rem;
    background: rgba(0, 212, 170, 0.1);
    padding: 0.2rem 0.6rem;
    border-radius: 1rem;
}

.skill__level {
    color: var(--color-primary);
    font-weight: 600;
}

.skill__bar {
    height: 8px;
    background: rgba(255, 255, 255, 0.1);
    border-radius: 4px;
    overflow: hidden;
}

.skill__progress {
    height: 100%;
    background: linear-gradient(90deg, var(--color-primary), var(--color-accent));
    border-radius: 4px;
    width: 0%;
    animation: fillProgress 1.5s ease-out forwards;
}

@keyframes fillProgress {
    from {
        width: 0%;
    }

    to {
        width: var(--skill-width, 0%);
    }
}

/* Updated Journey Timeline Styles */
.journey__timeline {
    position: relative;
    max-width: 900px;
    margin: 0 auto;
    padding: 2rem 0;
}

.journey__timeline::before {
    content: '';
    position: absolute;
    left: 50%;
    top: 0;
    bottom: 0;
    width: 3px;
    background: linear-gradient(180deg, var(--color-primary), var(--color-accent));
    transform: translateX(-50%);
    border-radius: 2px;
}

.timeline__item {
    position: relative;
    margin-bottom: 4rem;
    display: flex;
    align-items: center;
    width: 100%;
}

/* Left side items (odd) */
.timeline__item:nth-child(odd) {
    justify-content: flex-start;
}

.timeline__item:nth-child(odd) .timeline__content {
    margin-right: calc(50% + 60px);
    text-align: right;
}

.timeline__item:nth-child(odd) .timeline__year {
    order: 2;
}

/* Right side items (even) */
.timeline__item:nth-child(even) {
    justify-content: flex-end;
}

.timeline__item:nth-child(even) .timeline__content {
    margin-left: calc(50% + 60px);
    text-align: left;
}

.timeline__item:nth-child(even) .timeline__year {
    order: 1;
}

.timeline__year {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    width: 90px;
    height: 90px;
    background: linear-gradient(135deg, var(--color-primary), var(--color-accent));
    color: var(--color-white);
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: 700;
    font-size: 1.3rem;
    z-index: 2;
    box-shadow: 0 8px 25px rgba(77, 181, 255, 0.3);
    border: 4px solid var(--color-bg);
    transition: var(--transition);
}

.timeline__year:hover {
    transform: translateX(-50%) scale(1.1);
    box-shadow: 0 12px 35px rgba(77, 181, 255, 0.4);
}

.timeline__content {
    flex: 1;
    max-width: 400px;
    background: rgba(255, 255, 255, 0.08);
    border: 1px solid rgba(77, 181, 255, 0.2);
    border-radius: 16px;
    padding: 2rem;
    position: relative;
    transition: var(--transition);
    backdrop-filter: blur(10px);
}

.timeline__content:hover {
    background: rgba(255, 255, 255, 0.12);
    border-color: var(--color-primary);
    transform: translateY(-8px);
    box-shadow: 0 15px 40px rgba(0, 0, 0, 0.2);
}

/* Arrow indicators */
.timeline__content::before {
    content: '';
    position: absolute;
    top: 50%;
    width: 0;
    height: 0;
    border: 12px solid transparent;
}

/* Left side arrows (pointing right) */
.timeline__item:nth-child(odd) .timeline__content::before {
    right: -24px;
    border-left-color: rgba(77, 181, 255, 0.2);
    transform: translateY(-50%);
}

/* Right side arrows (pointing left) */
.timeline__item:nth-child(even) .timeline__content::before {
    left: -24px;
    border-right-color: rgba(77, 181, 255, 0.2);
    transform: translateY(-50%);
}

.timeline__content h4 {
    color: var(--color-white);
    font-size: 1.4rem;
    margin-bottom: 1rem;
    font-weight: 600;
    line-height: 1.3;
}

.timeline__content p {
    color: var(--color-light);
    line-height: 1.7;
    font-size: 1rem;
}

/* Animation on scroll */
.timeline__item.fade-in .timeline__content {
    animation: slideInContent 0.8s ease-out forwards;
}

.timeline__item.fade-in .timeline__year {
    animation: slideInYear 0.6s ease-out forwards;
}

@keyframes slideInContent {
    from {
        opacity: 0;
        transform: translateY(30px);
    }

    to {
        opacity: 1;
        transform: translateY(0);
    }
}

@keyframes slideInYear {
    from {
        opacity: 0;
        transform: translateX(-50%) scale(0.5);
    }

    to {
        opacity: 1;
        transform: translateX(-50%) scale(1);
    }
}

/* Responsive Design for Timeline */
@media screen and (max-width: 1024px) {
    .journey__timeline {
        max-width: 700px;
    }

    .timeline__content {
        max-width: 350px;
        padding: 1.8rem;
    }

    .timeline__year {
        width: 80px;
        height: 80px;
        font-size: 1.2rem;
    }
}

@media screen and (max-width: 768px) {
    .journey__timeline::before {
        left: 40px;
        transform: none;
    }

    .timeline__item {
        flex-direction: row !important;
        justify-content: flex-start !important;
        padding-left: 100px;
        margin-bottom: 3rem;
    }

    .timeline__item:nth-child(odd) .timeline__content,
    .timeline__item:nth-child(even) .timeline__content {
        margin: 0;
        text-align: left;
        max-width: none;
    }

    .timeline__year {
        position: absolute;
        left: 0;
        transform: none;
        width: 70px;
        height: 70px;
        font-size: 1.1rem;
    }

    .timeline__content::before {
        display: none;
    }

    .timeline__content {
        margin-left: 1.5rem;
        padding: 1.5rem;
    }
}

@media screen and (max-width: 600px) {
    .journey__timeline::before {
        left: 30px;
    }

    .timeline__item {
        padding-left: 80px;
    }

    .timeline__year {
        width: 60px;
        height: 60px;
        font-size: 1rem;
        border: 3px solid var(--color-bg);
    }

    .timeline__content {
        margin-left: 1rem;
        padding: 1.2rem;
    }

    .timeline__content h4 {
        font-size: 1.2rem;
    }

    .timeline__content p {
        font-size: 0.95rem;
    }
}

@media screen and (max-width: 480px) {
    .journey__timeline::before {
        left: 25px;
    }

    .timeline__item {
        padding-left: 70px;
    }

    .timeline__year {
        width: 50px;
        height: 50px;
        font-size: 0.9rem;
    }

    .timeline__content {
        margin-left: 0.8rem;
        padding: 1rem;
    }
}